@import "vars";

.whatwedo {
  background-color: #fff;
  position: relative;
  text-align: center;

  &__name {
    font-style: normal;
    font-weight: 900;
    font-size: 48px;
    color: $heavy-blue;
    position: absolute;
    line-height: 1;
    top: -0.5em;
    left: 0;
    right: 0;
  }

  &__contant {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    padding-top: 136px;
    padding-bottom: 162px;
    text-align: left;
  }

  &__block {
    width: 264px;
    display: flex;
    flex-direction: column;
  }

  &__image {
    width: 160px;
    height: 160px;
    border-radius: 50%;
    margin-bottom: 64px;
    align-self: center;
  }

  &__title {
    font-style: normal;
    font-weight: 900;
    font-size: 20px;
    line-height: 27px;
    color: $heavy-blue;
    margin-bottom: 32px;
  }

  &__subtitle {
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 24px;
    color: $heavy-blue-definition;
  }

  @media (max-width: 999px) {
    & {
      &__name {
        font-size: 40px;
      }

      &__contant {
        justify-content: space-around;
        padding-bottom: 0;
      }

      &__block {
        margin-right: 32px;
        margin-bottom: 86px;

        &:last-child {
          margin-bottom: 60px;
        }
      }
    }
  }

  @media (max-width: 599px) {
    & {
      &__block {
        margin-right: 0;
      }
    }
  }
}
